import React from "react";
import WordTreeRow from "./wordtree.js";
import DashboardCard from "./dashboardcard.js";
import { Row, Col, Spinner } from "reactstrap";
import Graph from "./graph.js";
import JsonCard from "./jsoncard.js";
import LineChart from "./linechart.js";

class Dashboard extends React.Component {
  render() {
    return (
      <div>
        {this.props.isLoading ? (
          <div className="text-center m-10">
            <Spinner style={{ width: "6rem", height: "6rem" }} color="danger" />
          </div>
        ) : (
          <div>
            {/* plot counts data */}
            {this.props.datasets.length > 0 && this.props.datasets[0].plot && (
              <LineChart
                data={this.props.datasets[0].plot.data}
                categories={this.props.datasets[0].plot.categories}
              />
            )}

            {/* Entities Data */}
            <JsonCard
              data={this.props.datasets}
              field={"entities"}
              title={"Entities Json Data"}
            />

            {/* Statements graph GRAPH */}
            <Graph
              data={this.props.datasets}
              field={"graph"}
              title={"Plot Graph"}
            />

            {/* Statements graph json */}
            <JsonCard
              data={this.props.datasets}
              field={"statements"}
              title={"Graph Json Data"}
            />

            {/* word tree */}
            <WordTreeRow
              datasets={this.props.datasets}
              field={"tree"}
              title={"Word tree"}
            />
          </div>
        )}

        {/* dataset */}
        <Row>
          {this.props.datasets.map(
            dataset =>
              dataset.cause && (
                <Col sm="6" key={dataset.name}>
                  <DashboardCard
                    key={dataset.name}
                    title={"Causal Frames in text"}
                    body={dataset.name}
                    data={dataset.cause}
                  ></DashboardCard>
                </Col>
              )
          )}
        </Row>

        {/* dataset */}
        <Row>
          {this.props.datasets.map(dataset => (
            <Col sm="6" key={dataset.name}>
              <DashboardCard
                key={dataset.name}
                title={"Dataset"}
                body={dataset.name}
                data={dataset.data}
              ></DashboardCard>
            </Col>
          ))}
        </Row>
      </div>
    );
  }
}

export default Dashboard;
