import React from "react";
import { Row, Col, Card, CardBody, CardTitle } from "reactstrap";
import DatasetLabelBox from "./datasetLabelBox.js";
import Modal from "./modal.js";
import Steps from "./steps.js";

class Pipeline extends React.Component {
  render() {
    return (
      <Row>
        <Col>
          <Card style={{ height: "200px" }} className="shadow">
            <CardBody>
              <CardTitle> Choose dataset(s)</CardTitle>
              <Modal
                disabled={this.props.labels.length >= 2}
                buttonLabel="Add Dataset"
                onAdd={this.props.onAdd}
              />
              <DatasetLabelBox
                onDelete={this.props.onDelete}
                labels={this.props.labels}
              ></DatasetLabelBox>
            </CardBody>
          </Card>
        </Col>

        {/* this.props.labels.length > 0 &&  */}
        {
          <Steps
            steps={this.props.steps}
            pipeline={this.props.pipeline}
            onStatement={this.props.onStatement}
            onWordTree={this.props.onWordTree}
            onCause={this.props.onCause}
            onPlotGraph={this.props.onPlotGraph}
            onEntities={this.props.onEntities}
            onPlot={this.props.onPlot}
            onFinish={this.props.onFinish}
            onTextFunction5={this.props.onTextFunction5}
            onTextFunction6={this.props.onTextFunction6}
          ></Steps>
        }
      </Row>
    );
  }
}

export default Pipeline;
