import React from "react";
import Chart from "react-google-charts";
import { Card, Col, Row } from "reactstrap";

class WordTreeRow extends React.Component {
  render() {
    if (!this.props.datasets) return null;
    return (
      <Row>
        {this.props.datasets.map(
          dataset =>
            dataset[this.props.field] && (
              <WordTree
                key={dataset.name}
                data={dataset[this.props.field]}
                focusWord={dataset.term}
              />
            )
        )}
      </Row>
    );
  }
}

class WordTree extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      focusWord: null
    };
  }

  componentDidMount() {
    this.setState({
      data: this.props.data,
      focusWord: this.props.focusWord
    });
  }

  render() {
    return (
      <Col sm="6">
        <Card className="shadow justify-content-between">
          <Chart
            loader={<div>Loading Chart</div>}
            chartType="WordTree"
            data={this.state.data}
            options={{
              wordtree: {
                format: "implicit",
                word: this.state.focusWord,
                type: "suffix"
              }
            }}
            rootProps={{ "data-testid": "1" }}
          />
        </Card>
      </Col>
    );
  }
}

export default WordTreeRow;
