import React from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row
} from "reactstrap";

class ResultSteps extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false
    };
    this.toggle = this.toggle.bind(this);
  }

  publish() {
    this.toggle();
  }

  toggle() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  }

  handleChange({ target }) {
    this.setState({
      [target.name]: target.value
    });
  }

  render() {
    return (
      <div>
        <Button
          size="sm"
          disabled={this.props.disabled}
          className="mb-2"
          color="danger"
          onClick={this.toggle}
          block
        >
          Check pipeline steps
        </Button>
        <Modal isOpen={this.state.isModalOpen} toggle={this.toggle}>
          <ModalHeader toggle={this.toggle}>
            Pipeline steps overview{" "}
          </ModalHeader>
          <ModalBody>
            <ul>
              {this.props.pipeline.map(item => (
                <li key={item}>{item}</li>
              ))}
            </ul>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggle}>
              Cancel
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}
export default ResultSteps;
