import React, { Component } from "react";
import { Col, Card, Row, CardBody, Label, FormGroup } from "reactstrap";

import HighchartsReact from "highcharts-react-official";
import Highcharts from "highcharts";
import { DebounceInput } from "react-debounce-input";

class LineChart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      inputValue: "",
      // To avoid unnecessary update keep all options in the state.
      chartOptions: {
        chart: { type: "area" },
        xAxis: {
          maxPadding: 0,
          type: "category",
          crosshair: true,
          categories: this.props.categories
        },
        title: { text: "Named Entities frequencies over time" },
        plotOptions: {
          area: {
            stacking: "normal",
            lineColor: "#666666",
            lineWidth: 1,
            marker: { lineWidth: 1, lineColor: "#666666" }
          }
        },
        series: this.props.data
      }
    };
  }

  setHoverData = e => {
    // The chart is not updated because `chartOptions` has not changed.
    this.setState({ hoverData: e.target.category });
  };

  //   updateSeries = () => {
  //     // The chart is updated only with new options.
  //     this.setState({
  //       chartOptions: {
  //         series: [{ data: [Math.random() * 5, 2, 1] }]
  //       }
  //     });
  //   };

  updateInput = evt => {
    this.setState({ inputValue: evt.target.value }, () => {
      let itemsToPlot = this.props.data.filter(data =>
        data.name.toLowerCase().includes(this.state.inputValue)
      );
      //   let itemsToPlot = this.props.data.filter(jsonObj => {
      //     if (jsonObj.name.toLowerCase().includes(this.state.inputValue)) {
      //       return jsonObj;
      //     }
      //   });
      this.setState(prevState => ({
        ...prevState,
        chartOptions: {
          ...prevState.chartOptions,
          series: itemsToPlot
        }
      }));
    });
  };

  render() {
    const { chartOptions } = this.state;
    return (
      <Row>
        <Col>
          <Card className="shadow justify-content-between">
            <CardBody>
              <Row form>
                <Col md={2}>
                  <FormGroup>
                    <Label for="searchInput">Search terms containing: </Label>
                    <DebounceInput
                      id="searchInput"
                      className="form-control"
                      value={this.state.inputValue}
                      minLength={3}
                      debounceTimeout={900}
                      onChange={evt => this.updateInput(evt)}
                    />
                  </FormGroup>
                </Col>
              </Row>
              <HighchartsReact highcharts={Highcharts} options={chartOptions} />
              {/* <button onClick={this.updateSeries.bind(this)}>Update Series</button> */}
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

export default LineChart;
