/* eslint react/no-multi-comp: 0, react/prop-types: 0 */

import React from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Label,
  Col,
  Input,
  Row,
  FormGroup
} from "reactstrap";

class ModalExample extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false,
      term: "floods",
      number: "10",
      start: "2018-1-1",
      stop: "2019-1-1",
      source: "guardian"
    };
    this.toggle = this.toggle.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.publish = this.publish.bind(this);
  }

  publish() {
    this.toggle();
    this.props.onAdd(
      this.state.source,
      this.state.term,
      this.state.start,
      this.state.stop,
      this.state.number
    );
  }

  toggle() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  }

  handleChange({ target }) {
    this.setState({
      [target.name]: target.value
    });
  }

  render() {
    return (
      <div>
        <Button
          size="sm"
          disabled={this.props.disabled}
          className="mb-2"
          color="danger"
          onClick={this.toggle}
          block
        >
          {this.props.buttonLabel}
        </Button>
        <Modal isOpen={this.state.isModalOpen} toggle={this.toggle}>
          <ModalHeader toggle={this.toggle}>Add new dataset</ModalHeader>
          <ModalBody>
            <Row form>
              <Col md={10}>
                <FormGroup>
                  <Label for="sourceInput">Source</Label>
                  <Input
                    value={this.state.source}
                    type="select"
                    name="source"
                    id="sourceInput"
                    onChange={this.handleChange}
                  >
                    <option value="guardian">Guardian</option>
                    <option value="twitter">Twitter</option>
                    <option value="reddit">Reddit</option>
                    <option value="parlgbr">Parliament UK</option>
                    <option value="parlger">Parliament DE</option>
                  </Input>
                </FormGroup>
              </Col>
            </Row>
            <Row form>
              <Col md={10}>
                <FormGroup>
                  <Label for="termInput">Term</Label>
                  <Input
                    type="input"
                    name="term"
                    id="termInput"
                    onChange={this.handleChange}
                    placeholder={this.state.term}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row form>
              <Col md={10}>
                <FormGroup>
                  <Label for="numberInput">Number</Label>
                  <Input
                    type="input"
                    name="number"
                    id="numberInput"
                    onChange={this.handleChange}
                    placeholder={this.state.number}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row form>
              <Col md={6}>
                <FormGroup>
                  <Label for="startInput">From</Label>
                  <Input
                    type="input"
                    name="start"
                    id="startInput"
                    onChange={this.handleChange}
                    placeholder={this.state.start}
                  />
                </FormGroup>
              </Col>
              <Col md={6}>
                <FormGroup>
                  <Label for="stopInput">To</Label>
                  <Input
                    type="input"
                    name="stop"
                    id="stopInput"
                    onChange={this.handleChange}
                    placeholder={this.state.stop}
                  />
                </FormGroup>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="danger" onClick={this.publish}>
              Add Dataset
            </Button>{" "}
            <Button color="secondary" onClick={this.toggle}>
              Cancel
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default ModalExample;
