export const BASE_URL = "https://penelope.vub.be/observatory-api/";
//export const BASE_URL = "http://localhost:5000/";

export const HEADERS = {
  Accept: "application/json",
  "Content-Type": "application/json"
};

export default function getURL(source, term, start, stop, number) {
  const path = {
    guardian: "test?",
    parlgbr: "get-parl-data-gbr?",
    parlger: "get-parl-data-ger?",
    twitter: "twitter-data?",
    reddit: "get-reddit-data?"
  };
  let params = new URLSearchParams({
    term: term,
    start: start,
    stop: stop,
    number: number
  });
  return BASE_URL + path[source] + params.toString();
}

export function appendDataset(term, start, stop, number, data, prevState) {
  let datasetName = term + start + stop + number;
  let dataset = { ...prevState.dataset };
  let step = prevState.step;

  if (!prevState.step.length) step = [...prevState.step, "text"];

  dataset[datasetName] = {
    term: term,
    start: start,
    stop: stop,
    number: number,
    data: data
  };
  return { dataset, step: step };
}
