import React, { Fragment } from "react";
import { Col, Button } from "reactstrap";
import PipelineCard from "./pipelinecard.js";
import StepProgressBar from "./progressbar.js";
import ResultSteps from "./resultsteps.js";

// class ButtonGroup extends React.Component {}

class Steps extends React.Component {
  constructor(props) {
    super(props);
    this.handlePlot = this.handlePlot.bind(this);
    this.handleFinal = this.handleFinal.bind(this);
    this.handleCause = this.handleCause.bind(this);
    this.handleEntities = this.handleEntities.bind(this);
    this.handleWordTree = this.handleWordTree.bind(this);
    this.handleStatement = this.handleStatement.bind(this);
    this.handlePlotGraph = this.handlePlotGraph.bind(this);
    this.handleClickButton5 = this.handleClickButton5.bind(this);
    this.handleClickButton6 = this.handleClickButton6.bind(this);

    this.state = {
      pipeline: null
    };
  }

  handleWordTree() {
    this.props.onWordTree();
  }

  handleStatement() {
    this.props.onStatement();
  }

  handleEntities() {
    this.props.onEntities();
  }

  handleCause() {
    console.log("getting causes");
    this.props.onCause();
  }

  handlePlotGraph() {
    this.props.onPlotGraph();
  }

  handlePlot() {
    this.props.onPlot();
  }

  handleClickButton5() {
    this.props.onTextFunction5();
  }

  handleClickButton6() {
    this.props.onTextFunction6();
  }

  handleFinal() {
    alert("Pipeline steps:");
    this.setState({ pipeline: this.props.pipeline });
  }

  render() {
    const buttons = {
      text: (
        <div>
          <Button block size="sm" color="danger" onClick={this.handleStatement}>
            Statement Graph
          </Button>
          <Button block size="sm" color="danger" onClick={this.handleEntities}>
            Named Entities
          </Button>
          <Button block size="sm" color="danger" onClick={this.handleCause}>
            Causal Frames
          </Button>
          <Button block size="sm" color="danger" disabled={true}>
            N-grams
          </Button>
          <Button block size="sm" color="danger" onClick={this.handleWordTree}>
            Word tree
          </Button>
        </div>
      ),
      numerical: (
        <div>
          <Button block size="sm" color="danger" onClick={this.handlePlot}>
            Plot counts
          </Button>
        </div>
      ),
      graph: (
        <div>
          <Button block size="sm" color="danger" onClick={this.handlePlotGraph}>
            Plot graph
          </Button>
        </div>
      ),
      visual: (
        <div>
          <Button
            block
            size="sm"
            color="danger"
            onClick={this.handleClickButton5}
          >
            Plot graph
          </Button>
          <Button
            block
            size="sm"
            color="danger"
            onClick={this.handleClickButton6}
          >
            Plot counts
          </Button>
        </div>
      ),
      final: (
        <div>
          {/* <Button block size="sm" color="danger" onClick={this.handleFinal}>
            Print Pipeline Steps
          </Button> */}
          <ResultSteps pipeline={this.props.pipeline} />
        </div>
      )
    };

    return (
      <Fragment>
        <Col xs="1">
          <StepProgressBar
            percent={this.props.steps.length > 1 && 100}
          ></StepProgressBar>
        </Col>
        <Col>
          <PipelineCard
            title={"Step 1"}
            body={buttons[this.props.steps[0]]}
          ></PipelineCard>
        </Col>
        <Col xs="1">
          <StepProgressBar
            percent={this.props.steps.length > 2 && 100}
          ></StepProgressBar>
        </Col>
        <Col>
          <PipelineCard
            title={"Step 2"}
            body={buttons[this.props.steps[1]]}
          ></PipelineCard>
        </Col>
        <Col xs="1">
          <StepProgressBar
            percent={this.props.steps.length > 3 && 100}
          ></StepProgressBar>
        </Col>
        <Col>
          <PipelineCard
            title={"Step 3"}
            body={buttons[this.props.steps[2]]}
          ></PipelineCard>
        </Col>
      </Fragment>
    );
  }
}

export default Steps;

