import React from "react";
import MyNavbar from "./navbar.js";
import Pipeline from "./pipeline.js";
import Dashboard from "./dashboard.js";
import Home from "./home.js";
import About from "./about.js";
import { Container } from "reactstrap";
import getURL, { BASE_URL, HEADERS } from "./createURLquery.js";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Warning from "./warning.js";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      datasets: [],
      steps: [],
      pipeline: [],
      loading: false,
      errorMessage: ""
    };
    this.handleAdd = this.handleAdd.bind(this);
    this.handlePlot = this.handlePlot.bind(this);
    this.handleGraph = this.handleGraph.bind(this);
    this.handleCause = this.handleCause.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleFinish = this.handleFinish.bind(this);
    this.handleWordTree = this.handleWordTree.bind(this);
    this.handleEntities = this.handleEntities.bind(this);
    this.handleStatement = this.handleStatement.bind(this);
    this.handleFunction5 = this.handleFunction5.bind(this);
    this.handleFunction6 = this.handleFunction6.bind(this);
  }

  handleAdd(source, term, start, stop, number) {
    const name = source + term + start + stop + number;
    if (this.state.datasets.some(e => e.name === name)) return;

    let url = getURL(source, term, start, stop, number);
    this.setState({ pipeline: [...this.state.pipeline, "Dataset: " + url] });
    fetch(url, {
      method: "GET"
    })
      .then(data => data.json())
      .then(data => {
        this.setState(previousState => ({
          datasets: [
            ...previousState.datasets.map(dataset => {
              dataset.statements = null;
              dataset.graph = null;
              dataset.plot = null;
              dataset.tree = null;
              dataset.entities = null;
              return dataset;
            }),
            {
              name: name,
              data: data,
              term: term,
              start: start,
              stop: stop,
              number: number
            }
          ],
          steps: ["text"]
        }));
      });
  }

  handleDelete(toDelete) {
    const datasets = this.state.datasets.filter(set => set.name !== toDelete);
    const newDatasets = datasets.map(dataset => {
      dataset.statements = null;
      dataset.graph = null;
      dataset.plot = null;
      dataset.tree = null;
      dataset.entities = null;
      return dataset;
    });
    if (datasets.length === 0) {
      this.setState({ steps: [], datasets: newDatasets });
    } else {
      this.setState({ steps: ["text"], datasets: newDatasets });
    }
  }

  handleWordTree() {
    this.setState({ loading: true });
    const dataArr = this.state.datasets.map(dataset => dataset.data);

    let url = BASE_URL + "get-word-tree";

    this.setState({ pipeline: [...this.state.pipeline, "Word Tree: " + url] });

    const fetches = dataArr.map(data =>
      fetch(BASE_URL + "get-word-tree", {
        method: "POST",
        headers: HEADERS,
        body: JSON.stringify(data)
      })
    );
    Promise.all(fetches)
      .then(responses => Promise.all(responses.map(r => r.json())))
      .then(jsonObjects => {
        this.setState(prevState => ({
          datasets: [
            ...prevState.datasets.map((dataset, i) => {
              dataset.tree = jsonObjects[i];
              return dataset;
            })
          ]
        }));
        this.setState({ steps: ["text", "final"] });

        //TODO TEST IF DASHBOARD UPDATES PROPERLY
        this.setState(prevState => {
          return prevState.datasets.map(dataset => {
            dataset.statements = null;
            dataset.graph = null;
            dataset.plot = null;
            dataset.entities = null;
            return dataset;
          });
        });

        this.setState({ loading: false });
      });
  }

  handleStatement() {
    this.setState({ loading: true });
    const dataArr = this.state.datasets.map(dataset => dataset.data);

    let url = BASE_URL + "get-statements";

    this.setState({ pipeline: [...this.state.pipeline, "Statements: " + url] });

    const fetches = dataArr.map(data =>
      fetch(BASE_URL + "get-statements", {
        method: "POST",
        headers: HEADERS,
        body: JSON.stringify(data)
      })
    );
    Promise.all(fetches)
      .then(responses => Promise.all(responses.map(r => r.json())))
      .then(jsonObjects => {
        this.setState(prevState => ({
          datasets: [
            ...prevState.datasets.map((dataset, i) => {
              dataset.statements = jsonObjects[i];
              return dataset;
            })
          ]
        }));
        this.setState({ steps: ["text", "graph"] });

        //TODO TEST IF DASHBOARD UPDATES PROPERLY
        this.setState(prevState => {
          return prevState.datasets.map(dataset => {
            dataset.tree = null;
            dataset.graph = null;
            dataset.plot = null;
            dataset.entities = null;
            return dataset;
          });
        });

        this.setState({ loading: false });
      });
  }

  handleGraph() {
    this.setState({ loading: true });

    let url = BASE_URL + "visualize-graph";

    this.setState({
      pipeline: [...this.state.pipeline, "Graph Visualisation: " + url]
    });

    this.setState(prevState => ({
      datasets: [
        ...prevState.datasets.map(dataset => {
          dataset.graph = dataset.statements;
          return dataset;
        })
      ]
    }));
    this.setState({ steps: ["text", "graph", "final"] });
    this.setState({ loading: false });
  }

  handleEntities() {
    this.setState({ loading: true });
    const dataArr = this.state.datasets.map(dataset => dataset.data);

    let url = BASE_URL + "get-entities";

    this.setState({
      pipeline: [...this.state.pipeline, "Named Entity Recognition: " + url]
    });

    const fetches = dataArr.map(data =>
      fetch(BASE_URL + "get-entities", {
        method: "POST",
        headers: HEADERS,
        body: JSON.stringify(data)
      })
    );
    Promise.all(fetches)
      .then(responses => Promise.all(responses.map(r => r.json())))
      .then(jsonObjects => {
        this.setState(prevState => ({
          datasets: [
            ...prevState.datasets.map((dataset, i) => {
              dataset.entities = jsonObjects[i];
              return dataset;
            })
          ]
        }));
        this.setState({ steps: ["text", "numerical"] });
        //TODO TEST IF DASHBOARD UPDATES PROPERLY
        this.setState(prevState => {
          return prevState.datasets.map(dataset => {
            dataset.statements = null;
            dataset.graph = null;
            dataset.plot = null;
            dataset.tree = null;
            return dataset;
          });
        });

        this.setState({ loading: false });
      });
  }

  handlePlot() {
    this.setState({ loading: true });

    let url = "Plot graph";

    this.setState({ pipeline: [...this.state.pipeline, url] });

    this.setState(prevState => ({
      datasets: [
        ...prevState.datasets.map(dataset => {
          dataset.plot = dataset.entities;
          return dataset;
        })
      ]
    }));
    this.setState({ steps: ["text", "numerical", "final"] });
    this.setState({ loading: false });
  }

  handleFinish() {
    this.setState({ steps: [...this.state.steps, "done"] });
  }

  handleCause() {
    this.setState({ loading: true });

    const dataArr = this.state.datasets.map(dataset => dataset.data);
    const fetches = dataArr.map(data =>
      fetch(BASE_URL + "get-cause", {
        method: "POST",
        headers: HEADERS,
        body: JSON.stringify(data)
      })
    );
    Promise.all(fetches)
      .then(responses => Promise.all(responses.map(r => r.json())))
      .then(jsonObjects => {
        this.setState(prevState => ({
          datasets: [
            ...prevState.datasets.map((dataset, i) => {
              dataset.cause = jsonObjects[i];
              return dataset;
            })
          ]
        }));
        this.setState({ steps: ["text", "numerical"] });
        this.setState(prevState => {
          return prevState.datasets.map(dataset => {
            dataset.statements = null;
            dataset.graph = null;
            dataset.plot = null;
            dataset.tree = null;
            dataset.entities = null;
            return dataset;
          });
        });

        this.setState({ loading: false });
      })
      .catch(error => {
        this.setState({ errorMessage: "error" });
      });
  }

  handleFunction5() {}

  handleFunction6() {}

  render() {
    return (
      <>
        <Router basename={"/observatories/climate-change-opinion-observatory"}>
          <MyNavbar />
          <Switch>
            <Route path="/observatory">
              <Container fluid={true}>
                {/* <Warning message={this.state.errorMessage} /> */}
                <h1>Pipeline</h1>
                <Pipeline
                  labels={this.state.datasets.map(x => x.name)}
                  steps={this.state.steps}
                  pipeline={this.state.pipeline}
                  onAdd={this.handleAdd}
                  onDelete={this.handleDelete}
                  onStatement={this.handleStatement}
                  onWordTree={this.handleWordTree}
                  onCause={this.handleCause}
                  onEntities={this.handleEntities}
                  onPlotGraph={this.handleGraph}
                  onFinish={this.handleFinish}
                  onPlot={this.handlePlot}
                  onTextFunction5={this.handleFunction5} //free
                  onTextFunction6={this.handleFunction6} //free
                />
                <h1>Dashboard</h1>
                <Dashboard
                  isLoading={this.state.loading}
                  datasets={this.state.datasets}
                />
              </Container>
            </Route>
            <Route path="/about">
              <About />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Router>
      </>
    );
  }
}

export default App;
